@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:400,700);
/* 
font-family: 'Poppins', sans-serif;
font-family: 'Open Sans', sans-serif;
*/

body {
  margin: 0;
  padding: 0;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  margin: 10px auto;
}

.logo {
  background-image: url(/huddle/static/media/logo.3cb86730.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 40px;
  width: 200px;
}

.button {
    font-size: 20px;
    border: 1px solid #ff52bf;
    border-radius: 30px;
    padding: 3px 20px;
    color: #ff52bf;
    font-family: 'Open Sans', sans-serif;
    cursor: pointer;
}
.button:hover{
  background-color: rgba(255, 82, 192, 0.1);
}
.button:active {
  color: #fff;
  background-color: #ff52bf;
}

.intro {
  text-align: center;
  color: 	#00252e;
}

.intro h2 {
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 50px;
  margin: 150px 0 0;
}
.description {
  font-family: 'Open Sans', sans-serif;
  max-width: 440px;
  margin: 40px auto;
  font-size: 14px;
}

.screen-mockups {
  background-image: url(/huddle/static/media/screen-mockups.88090ace.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: -moz-available;
  height: -webkit-fill-available;
  height: stretch;
  margin-top: 100px;
}

.stats {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 80px;
}

.community,
.messages {
  text-align: left;
}
.community-icon {
  background-image: url(/huddle/static/media/icon-communities.4918e149.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 50px;
  height: 50px;
}
.messages-icon {
  background-image: url(/huddle/static/media/icon-messages.1203b299.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 50px;
  height: 50px;
}
.stats-heading {
  font-family: 'Open Sans', sans-serif;
  font-size: 60px;
  font-weight: 700;
  margin: 0;
  line-height: 60px;
}
.stats-description {
  font-family: 'Open Sans', sans-serif;
  opacity: 0.5;
}

@media (max-width: 512px) {
  .stats {
    flex-direction: column;
  }
  .messages {
    margin-top: 100px;
  }
}
.button-primary {
  font-family: 'Open Sans', sans-serif;
  color: #fff;
  background-color: #ff52bf;
  border: 1px solid #ff52bf;
  border-radius: 30px;
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  padding: 15px 70px;
  box-shadow: 2px 2px 10px 0px grey;
  cursor: pointer;
}

.button-primary:hover {
  background-color: rgba(255, 82, 192, 0.7);
}

.button-primary:active {
  color: #ff52bf;
  background-color: #fff;
}
.section1 {
  background-color:hsl(207, 100%, 98%);
}
.section1:before,
.section1:after {
  content: '';
  background-size: contain;
  width: 100%;
  height: 200px;
  background-repeat: no-repeat;
  position: absolute;
  display: inline-block;
}
.section1:before {
  background-image: url(/huddle/static/media/bg-section-top-desktop-1.bd9436af.svg);
  background-position: bottom;
  top: -200px;
}
.section1:after {
  background-image: url(/huddle/static/media/bg-section-bottom-desktop-1.3bd921c0.svg);
  background-position: top;
  bottom: -200px;
}

.illustration-grow {
  background-image: url(/huddle/static/media/illustration-grow-together.bb27da81.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 50%;
  height: 75%;
}

@media (max-width: 375px) {
  .section1:before {
    background-image: url(/huddle/static/media/bg-section-top-mobile-1.8acafd86.svg);
  }
  .section1:after {
    background-image: url(/huddle/static/media/bg-section-bottom-mobile-1.6ef9dec4.svg);
  }
}
.section2 {
  background-color: #fff;
}

.illustration-flowing {
  background-image: url(/huddle/static/media/illustration-flowing-conversation.6641e825.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 50%;
  height: 75%;
}
.section3 {
  background-color:hsl(207, 100%, 98%);
}
.section3:before,
.section3:after {
  content: '';
  background-size: contain;
  width: 100%;
  height: 200px;
  background-repeat: no-repeat;
  position: absolute;
  display: inline-block;
}
.section3:before {
  background-image: url(/huddle/static/media/bg-section-top-desktop-2.b50e8db3.svg);
  background-position: bottom;
  top: -200px;
}
.section3:after {
  background-image: url(/huddle/static/media/bg-section-bottom-desktop-2.775fc190.svg);
  background-position: top;
  bottom: -200px;
}

.illustration-users {
  background-image: url(/huddle/static/media/illustration-your-users.8a5af995.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 50%;
  height: 75%;
}

@media (max-width: 375px) {
  .section2:before {
    background-image: url(/huddle/static/media/bg-section-top-mobile-2.035f180f.svg);
  }
  .section2:after {
    background-image: url(/huddle/static/media/bg-section-bottom-mobile-2.8669861a.svg);
  }
}
.section {
  position: relative;
  height: 500px;
  margin: 0 auto;
  margin-top: 200px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 50px;
}

.text {
  width: 50%;
}

.text h3 {
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 25px;
  color: 	#00252e;
}
.text p {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  width: 80%;
  color: 	#00252e;
}
.section4 {
  text-align: center;
  height: 500px;
  margin-top: 200px;
}
.section4 h3 {
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 40px;
  color: 	#00252e;
}

@media (max-width: 900px) {
  .text {
    width: 45%;
  }
  .text p {
    width: 100%;
  }
}

@media (max-width: 700px) {
  .section {
    flex-direction: column-reverse;
  }
  .section div {
    width: 100%;
    text-align: center;
  }
}
.footer {
  position: relative;
  height: 500px;
  margin: 0 auto;
  margin-top: 200px;
  display: flex;
  justify-content: space-evenly;
  padding-top: 60px;
  background-color: hsl(192, 100%, 9%);
  color: #fff;
  font-family: 'Open Sans', sans-serif;
}

.footer:before {
  content: '';
  background-size: contain;
  width: 100%;
  height: 200px;
  background-repeat: no-repeat;
  position: absolute;
  display: inline-block;
  background-image: url(/huddle/static/media/bg-footer-top-desktop.6a2a7d2b.svg);
  background-position: bottom;
  top: -200px;
}

.footer .logo {
  -webkit-filter: brightness(100) saturate(0) contrast(100);
          filter: brightness(100) saturate(0) contrast(100);
}

.footer h3 {
  margin-top: 0;
}

.footer p {
  width: 75%;
}

.footer form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 25px;
}

.footer input {
  padding: 10px 15px;
  width: 65%;
  border-radius: 5px;
}

.footer .button-primary {
  border-radius: 5px;
  font-size: 14px;
  padding: 10px 50px;
  box-shadow: none;
}

.footer .fa-phone {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.footer .fa-w-16 {
  margin-right: 10px;
}

.footer .fa-w-14 {
  margin: 50px 15px 0 0;
  font-size: 30px;
  cursor: pointer;
}
.footer-section {
  padding: 0 50px;
}

@media (max-width: 700px) {
  .footer {
    flex-direction: column-reverse;
  }
  .footer-section:first-child {
    margin-top: 50px;
    padding-bottom: 100px;
  }
}

@media (max-width: 600px) {
  .footer form {
    align-items: flex-end;
    flex-direction: column;
  }
  .footer .button-primary {
    margin-top: 10px;
  }
}
@media (max-width: 480px) {
  .footer {
    padding-top: 100px;
  }
}
@media (max-width: 480px) {
  .footer-section {
    padding: 0 20px;
  }
}
@media (max-width: 400px) {
  .footer input {
    width: 90%;
  }
  .footer p {
    width: 100%;
  }
}

@media (max-width: 375px) {
  .footer-section {
    padding: 0 10px;
  }
  .footer:before {
    background-image: url(/huddle/static/media/bg-footer-top-desktop.6a2a7d2b.svg);
  }
}
